'use client'; // 声明这是一个客户端组件

import React from'react';
import { Text } from '@/components/ui/text';
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/context/AuthContext';
import api from '@/lib/api'; // 导入API实例
import '@/app/globals.css';

export default function HomePage() {
  const router = useRouter();
  const { isAuthenticated } = useAuth();
  const [username, setUsername] = useState('');
  const [loading, setLoading] = useState(true); // 新增loading状态

  // 页面保护：未登录直接跳转
  useEffect(() => {
    if (!isAuthenticated) {
      router.replace('/login'); // 更推荐replace，防止回退
    }
  }, [isAuthenticated, router]);

  // 拉取用户信息
  useEffect(() => {
    const fetchUser = async () => {
      try {
        const response = await api.get('api/user/me');
        setUsername(response.data.username);
      } catch (error) {
        console.error('获取用户信息失败');
        setUsername('未知用户');
      } finally {
        setLoading(false);
      }
    };
    if (isAuthenticated) {
      fetchUser();
    }
  }, [isAuthenticated]);

  const handleBack = () => {
    window.history.back();
  };

  // 页面保护和加载状态
  if (!isAuthenticated || loading) {
    return (
      <div className="flex justify-center items-center h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-gray-900"></div>
      </div>
    );
  }

  return (
    <div className="flex justify-center items-center h-screen bg-gray-100 px-4 sm:px-8 relative">
      <Button
        variant="ghost"
        className="absolute top-4 left-4"
        onClick={handleBack}
      >
        返回上一页
      </Button>
      <Card className="w-full max-w-none p-6 bg-white shadow-md rounded-lg h-100%">
        <div className="flex items-center mb-4">
          <img
            src="https://example.com/avatar.jpg"
            alt="Avatar"
            className="w-16 h-16 rounded-full mr-4"
          />
          <div>
            <Text as="h2" className="text-lg font-bold">{username}</Text>
            <Text as="p" className="text-gray-600">机器学习课题组</Text>
          </div>
        </div>
        <div className="flex justify-between mb-6">
          <Button variant="ghost">创建课题组</Button>
          <Button variant="ghost">加入课题组</Button>
          <Button variant="destructive">退出课题组</Button>
        </div>
        <div className="flex justify-around flex-1">
          <div>
            <Text as="h3" className="text-2xl font-bold">28</Text>
            <Text as="p" className="text-gray-600">私人文献</Text>
          </div>
          <div>
            <Text as="h3" className="text-2xl font-bold">156</Text>
            <Text as="p" className="text-gray-600">公共文献</Text>
          </div>
          <div>
            <Text as="h3" className="text-2xl font-bold">12</Text>
            <Text as="p" className="text-gray-600">最近活动</Text>
          </div>
        </div>
      </Card>
    </div>
  );
}